@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

.menuBtn {
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.menuBtn div,
.menuBtn div:before,
.menuBtn div:after {
  background: #1a1a1a;
  border-radius: 10px;
  width: 40px;
  height: 5px;
  transition: all 0.15s linear;
}

.menuBtn div:before,
.menuBtn div:after {
  content: "";
  position: absolute;
}

.menuBtn div:before {
  transform: translateY(-200%);
}

.menuBtn div:after {
  transform: translateY(200%);
}

.menuBtn.open div {
  background: transparent;
}

.menuBtn.open div:before {
  transform: rotate(45deg);
}

.menuBtn.open div:after {
  transform: rotate(-45deg);
}

.top_nav {
  background: #f6f6f8;
}

.top_nav .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 60px;
  width: 100%;
}

.top_nav .top_nav__block {
  display: flex;
}

.top_nav .top_nav__block a {
  display: block;
  text-decoration: none;
  margin-right: 15px;
}

.top_nav .top_nav__block a svg {
  width: 45px;
  height: 45px;
  fill: #e3774c;
}

.top_nav .top_nav__block a:last-child {
  margin-right: 0;
}

.top_nav .top_nav__block a {
  font-weight: bold;
}

nav {
  background: #fff;
}

nav .container {
  display: flex;
  justify-content: space-between;
  min-height: 80px;
  align-items: center;
  position: relative;
}

nav .container .nav_menu .menuBtn {
  display: none;
}

nav .container .nav_menu ul {
  display: flex;
  justify-content: space-between;
}

nav .container .nav_menu ul li {
  list-style-type: none;
}

nav .container .nav_menu ul li a {
  display: flex;
  height: 80px;
  align-items: center;
  padding: 8px;
  color: #1a1a1a;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
  z-index: 5;
  transition: color 0.4s;
}

nav .container .nav_menu ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0%;
  top: 0;
  left: 0;
  background: #e3774c;
  transition: height 0.3s;
  z-index: -1;
}

nav .container .nav_menu ul li a:hover {
  color: #fff;
}

nav .container .nav_menu ul li a:hover::after {
  height: 100%;
}

