@font-face {
  font-family: NSABold;
  src: url("../fonts/NotoSansArmenian-Bold.ttf");
}

@font-face {
  font-family: NSARegular;
  src: url("../fonts/NotoSansArmenian-Regular.ttf");
}

@font-face {
  font-family: NSAMedium;
  src: url("../fonts/NotoSansArmenian-Medium.ttf");
}

@font-face {
  font-family: NSALight;
  src: url("../fonts/NotoSansArmenian-Light.ttf");
}

@font-face {
  font-family: NSAThin;
  src: url("../fonts/NotoSansArmenian-Thin.ttf");
}

body {
  font-family: NSARegular, "Roboto", sans-serif;
}

body::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  overflow: hidden;
}

body::-webkit-scrollbar-track {
  background: #ccc;
}

body::-webkit-scrollbar-thumb {
  background: #e3774c;
}

p {
  line-height: 24px;
}

a {
  color: #4088FC;
  text-decoration: none;
}

a:hover {
  color: #e3774c;
}

@media screen and (max-width: 768px) {
  nav .container .nav_menu .menuBtn {
    display: flex;
  }

  nav .container .nav_menu ul {
    flex-flow: column;
    justify-content: flex-start;
    position: absolute;
    top: 0px;
    width: 500px;
    overflow: hidden;
    left: -5000px;
    background: #f6f6f8;
    transition: left 0.2s linear;
  }

  nav .container .nav_menu ul li a {
    height: 50px;
    padding: 8px 20px;
  }

  nav .container .nav_menu ul.active_nav {
    left: 0px;
    height: 100vh;
  }
}

@media screen and (max-width: 768px) and (max-width: 580px) {
  nav .container .nav_menu ul {
    width: 300px;
  }
}

@media screen and (max-width: 768px) and (max-width: 420px) {
  nav .container .nav_menu ul {
    width: 200px;
  }
}